<template>
  <aside class="menu section">
    <figure class="image logo">
      <img src="@/assets/logo.png">
    </figure>
    <p class="menu-label">
      Teams
    </p>
    <ul class="menu-list">
      <li v-for="(item, i) in allTeams" :key="i">
        <router-link :to=" '/team/' + item.slug" :class="{'is-active': $route.params.name ===  item.slug}">
          <div class="level">
            <div class="level-left">
              <span class="level-item lnr lnr-users"></span>
              <p class="level-item">{{item.name}}</p>
            </div>
          </div>  
        </router-link>
        <ul v-if="$store.state.activeTeam && $store.state.activeTeam.slug === item.slug">
          <li>
            <div class="level">
              <div class="level-left">
                <p class="level-item menu-label has-text-weight-bold">projects:</p>
              </div>
              <div class="level-right">
                <a class="level-item lnr lnr-plus-circle has-text-weight-semibold is-paddingless" @click="addProjectModal()"></a>
              </div>
            </div> 
          </li>
          <li v-for="(project, j) in teamProject" :key="j">
            <router-link :to="'/project/' + project.slug" :class="{'is-active' : $store.state.activeProject && $store.state.activeProject.slug === project.slug}">{{project.name}}</router-link>
          </li>
        </ul>
      </li>
    </ul>
    <a class="button is-small is-fullwidth is-link is-outlined is-rounded">Add more team</a>
  </aside>
</template>

<script>
import ProjectNew from '@/components/modal/ProjectNew'
export default {
  data () {
    return {
      teamProject: []
    }
  },

  components: {
    ProjectNew
  },

  watch: {
    '$route': 'getActiveContent',
    '$store.state.activeTeam': function () {
      this.teamProject = this.$store.getters.getTeamProjects
    }
  },

  computed: {
    allTeams () {
      return this.$store.state.teams
    },
    allProjects () {
      return this.$store.state.projects
    }
  },

  methods: {
    addProjectModal: function () {
      this.$modal.open({
        parent: this,
        component: ProjectNew
      })
    },
    getActiveContent: function () {
      if (this.$route.name === 'team') {
        this.$store.state.activeTeam = this.allTeams.find(team => team.slug === this.$route.params.name)
        this.$store.state.activeProject = null
      } else if (this.$route.name === 'project') {
        this.$store.state.activeProject = this.allProjects.find(project => project.slug === this.$route.params.name)        
        this.$store.state.activeTeam = this.allTeams.find(team => team.id === this.activeProject.team_id)
      }
    }
  }
}
</script>


<style lang="scss">
aside {
  .logo {
    width: 100px;
    margin-bottom: 1rem;
  }

  .menu-list {
    margin-bottom: 1.5rem;
  }

  li .menu-label {
    padding: .5em 0
  }
}
</style>
