=<template>
    <nav class="level navbar has-shadow is-marginless">
        <div class="level-left">
            <a class="button is-white level-item" @click="$emit('toggleSidebar')">
                <span class="icon is-small">
                    <span class="lnr" :class="isSidebarOpen ? 'lnr-cross' : 'lnr-menu'"></span>
                </span>
            </a>
            <a class="button is-white level-item">
                <span class="icon is-small">
                    <span class="lnr lnr-home"></span>
                </span>
            </a>
            <a class="button is-white level-item">My Tasks</a>
            <b-dropdown hoverable>
                <button class="button is-info" slot="trigger">
                    <span class="icon">
                        <span class="lnr lnr-plus-circle"></span>
                    </span>
                </button>

                <b-dropdown-item @click="addProjectModal()">New Project</b-dropdown-item>
                <b-dropdown-item>New Tasks</b-dropdown-item>
                <b-dropdown-item>New Discussion</b-dropdown-item>
                <b-dropdown-item>New Message</b-dropdown-item>
            </b-dropdown>            
        </div>
        <div class="level-item">
            <div class="control has-icons-left search">
                <input class="input is-rounded is-small" type="search" placeholder="Search...">
                <span class="icon is-left">
                    <i class="lnr lnr-magnifier"></i>
                </span>
            </div>
        </div>
        <div class="level-right">
            <b-dropdown position="is-bottom-left" class="level-item">
                <a class="button is-danger level-item" slot="trigger">
                    <span class="icon is-small">
                        <span class="lnr lnr-alarm"></span>
                    </span>
                </a>
                <b-dropdown-item custom paddingless>
                    <div class="card" style="width: 500px">
                        <div class="card-header">
                            <p class="card-header-title">Notification</p>
                            <a href="#" class="card-header-icon" aria-label="Notification settings">
                                <span class="icon">
                                    <span class="lnr lnr-cog"></span>
                                </span>
                            </a>
                        </div>
                        <div class="panel is-size-7 ">
                            <a class="panel-block has-text-weight-bold">
                                <span style="flex-grow:1">Danny Porter mentioned you in a comment</span>
                                <span>1h</span>
                            </a>
                            <a class="panel-block has-text-weight-bold">
                                <span style="flex-grow:1">You are assigned to a task</span>
                                <span>3h</span>
                            </a>
                            <a class="panel-block">
                                <span style="flex-grow:1">You are now member of project Research Development</span>
                                <span>4h</span>
                            </a>
                            <a class="panel-block">
                                <span style="flex-grow:1">You are now member of team General Research</span>
                                <span>1d</span>
                            </a>
                            <a class="panel-block">
                                <span style="flex-grow:1">Presley Hyatt added a file to a task you created</span>
                                <span>1d</span>
                            </a>
                            <a class="panel-block">
                                <span style="flex-grow:1">Juan Bennet added a comment to a task you follow</span>
                                <span>3d</span>
                            </a>
                            <a class="panel-block">
                                <span style="flex-grow:1">Nina Scott request new budget for a task</span>
                                <span>1w</span>
                            </a>
                        </div>
                    </div>
                </b-dropdown-item>
            </b-dropdown>
            <b-dropdown hoverable position="is-bottom-left">
                <a class="button is-white level-item" slot="trigger">
                    {{name}}
                    <figure class="image is-32x32 avatar">
                        <img class="is-rounded" :src=avatar>
                    </figure>
                </a>

                <b-dropdown-item>My Profile Settings</b-dropdown-item>
                <hr class="dropdown-divider">
                <b-dropdown-item>Log Out</b-dropdown-item>
            </b-dropdown>
        </div>
    </nav>
</template>

<script>
import ProjectNew from "@/components/modal/ProjectNew";
export default {
  name: 'navbar',
  
  props:['isSidebarOpen'],

  components: { 
      ProjectNew 
  },

  data () {
    return {
      name: faker.name.findName(),
      avatar: faker.internet.avatar()
    }
  },

  methods: {
    addProjectModal: function () {
      this.$modal.open({
        parent: this,
        component: ProjectNew
      })
    }
   }
}
</script>


<style scoped>
nav {
    padding: 0 1rem;
}
.search {
    width: 400px
}
</style>
      
